import ThinkTankCard from "@/components/Common/ThinkTankCard"
import { getArticleExperts } from '@/api/biunique'
export default {
  components:{
    ThinkTankCard,
  },
  data(){
    return{
      current: 0, //滑块当前索引
      active:0,
      list:[]
    }
  },
  mounted(){
    this.getList()
  },
  methods: {
    onChange(index) {
      this.current = index
    },
    swipeTo(index) {
      this.$refs.vanSwipe.swipeTo(index) 
    },
    async getList() {
      const { data:{code, data:{entries}} } = await getArticleExperts()
      if (code === 0){
        this.list = entries
      }
    }
  },
}
