import ThirdShare from '@/components/Common/ThirdShare'
import config from "@/config/common.config"
export default {
  components:{
    ThirdShare
  },
  props:{
    shareImgUrl:{
      type:String,
      required:true
    },
  },
  data() {
    return {
      shareOption:{},
      config
    }
  },
  mounted() {
    this.shareOption = {
      title : '科研背景提升',
      desc: `我已参加科研背景提升，你也来一起参加吧!`,
      imgUrl: this.shareImgUrl,
      link: `${config.LINK.school_h5}${this.$route.fullPath}`
    }
  },
  methods:{
    //打开分享窗口
    openShare(){
      this.$refs.ThirdShare.open()
    }
  }
}
